.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  top: -46px;
  right: 32px;
  width: 24px;
  height: 24px;
  font-size: 0;
  background-color: transparent;
  border: none;
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(-45deg);
}
.main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(45deg);
}
