.categories-list__title {
  margin: 0 0 30px 0;
  padding: 0 0 0 92px;
  position: relative;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 24px;
  font-weight: normal;
  line-height: 37px;
  color: #000000;
  text-transform: uppercase;
}
.categories-list__title::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
}
.categories-list__title--slim::after {
  left: 22px;
  top: -5px;
}
.categories-list__title--slim::before {
  content: "";
  position: absolute;
  width: 35px;
  height: 49px;
  top: -8px;
  left: 30px;
  background-image: url("../img/icon-cat-slim.svg");
  background-repeat: no-repeat;
  z-index: 10;
}
.categories-list__title--pro::after {
  top: -6px;
  left: 22px;
}
.categories-list__title--pro::before {
  content: "";
  position: absolute;
  width: 67px;
  height: 28px;
  top: 5px;
  left: 13px;
  background-image: url("../img/icon-cat-pro.svg");
  background-repeat: no-repeat;
  z-index: 10;
}
.categories-list__description {
  margin: 0 20px;
