  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
}
.categories-list__button:active {
  opacity: 0.3;
}
.categories-list__button--slim::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 97px;
  width: 24px;
  height: 11px;
  background-image: url("../img/icon-right-arrow-long.svg");
}
.categories-list__button--pro::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 95px;
  width: 24px;
  height: 11px;
  background-image: url("../img/icon-right-arrow-long.svg");
}
