  width: 190px;
  position: relative;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  color: #000000;
}
.form__fieldset-title--contacts {
  margin-bottom: 14px;
}
.form__fieldset-title--contacts::after {
  content: "";
  position: absolute;
  top: 14px;
  right: -110px;
  width: 99px;
  height: 3px;
  background-color: #68b738;
}
.additionals {
  margin: 0 auto;
  padding: 0 20px;
  width: 320px;
  margin-bottom: 10px;
  padding-top: 28px;
}
.additionals__title {
  margin: 0;
  padding: 0;
  position: relative;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  color: #000000;
}
.additionals__title::after {
  content: "";
  position: absolute;
  top: 14px;
  right: -20px;
  width: 53px;
  height: 3px;
  background-color: #68b738;
}
.additionals__additionals-list {
  margin: 0;
  padding: 34px 0 29px;
  list-style: none;
}
.additionals__item {
  padding-top: 14px;
  padding-bottom: 18px;
  border-top: 1px solid #cdcdcd;
}
.additionals__item:last-of-type {
  border-bottom: 1px solid #cdcdcd;
}
.additionals__item-title {
  margin: 0 0 13px 0;
  padding-left: 2px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
  word-wrap: break-word;
}
.additionals__item-table {
  margin-bottom: 14px;
  width: 100%;
  border-collapse: collapse;
}
.additionals__item-tr {
  display: flex;
  justify-content: space-between;
}
.additionals__item-td {
  padding: 0;
  line-height: 14px;
}
.additionals__item-td:first-of-type {
  text-align: left;
}
.additionals__item-td:last-of-type {
  text-align: right;
}
.additionals__button {
  margin: 0;
  padding: 11px;
  display: block;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  background-color: #68b738;
  word-wrap: break-word;
  word-break: break-all;
  padding-bottom: 8px;
  width: 100%;
  font-size: 16px;
  line-height: 20px;
}
.additionals__button:hover,
.additionals__button:focus {
  background-color: #5eaa2f;
}
.additionals__button:active {
  color: rgba(255, 255, 255, 0.3);
}
.promotion {
  margin: 0 auto;
  padding: 0 20px;
  width: 320px;
  margin-bottom: 35px;
}
.promotion__content {
  display: flex;
  flex-direction: column;
  min-height: 289px;
  background-color: #71b548;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-repeat: no-repeat;
}
.promotion__img {
  align-self: center;
  margin: 65px auto 62px;
}
.promotion__path {
  fill: #ffffff;
}
.promotion__description {
  margin: 0 auto;
  position: relative;
  width: 170px;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffffff;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  box-sizing: border-box;
}
body {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  font-family: "Arial", "Tahoma", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #444444;
}
